.top_nav {
  background: #f6f6f8;
}

.top_nav .container {
  width: 100%;
  min-height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.top_nav .top_nav__block {
  display: flex;
}

.top_nav .top_nav__block a {
  display: block;
  margin: 0px 15px 0px 0px;
}

.top_nav .top_nav__block a:last-child {
  margin: 0px 0px 0px 0px;
