nav {
  background: #fff;
  position: sticky;
  top: 0;
  left: 0;
  z-index: 999;
  box-shadow: 0 6px 36px rgba(0, 0, 0, 0.09);
}

nav .container {
  display: flex;
  justify-content: space-between;
  min-height: 80px;
  align-items: center;
  position: relative;
}

nav .container .nav_menu {
  overflow: hidden;
}

nav .container .nav_menu .menuBtn {
  display: none;
  cursor: pointer;
  width: 50px;
  height: 50px;
  justify-content: flex-end;
  align-items: center;
  position: relative;
}

nav .container .nav_menu .menuBtn:before,
nav .container .nav_menu .menuBtn::after {
  content: "";
  position: absolute;
  background-color: #1a1a1a;
  border-radius: 10px;
  width: 40px;
  height: 5px;
  transition: all 0.15s linear;
}

nav .container .nav_menu .menuBtn::before {
  transform: translateY(-200%);
}

nav .container .nav_menu .menuBtn::after {
  transform: translateY(200%);
}

nav .container .nav_menu .menuBtn span {
  position: absolute;
  width: 40px;
  height: 5px;
  background-color: #1a1a1a;
  border-radius: 10px;
  transition: transform 0.3s;
}

nav .container .nav_menu .menuBtn.open span {
  transform: scale(0);
}

nav .container .nav_menu .menuBtn.open::before {
  transform: rotate(45deg);
}

nav .container .nav_menu .menuBtn.open::after {
  transform: rotate(-45deg);
}

nav .container .nav_menu ul {
  display: flex;
  justify-content: space-between;
}

nav .container .nav_menu ul li a {
  display: flex;
  align-items: center;
  height: 80px;
  color: #1a1a1a;
  padding: 8px;
  letter-spacing: 1px;
  font-weight: bold;
  text-transform: uppercase;
  position: relative;
  z-index: 5;
  transition: color 0.3s;
}

nav .container .nav_menu ul li a::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0%;
  top: 0;
  left: 0;
  background: #f55d22;
  transition: height 0.4s;
  z-index: -1;
}

nav .container .nav_menu ul li a:hover {
  color: #fff;
}

nav .container .nav_menu ul li a:hover::after {
  height: 100%;
  background: #f55d22;
}

