header#home_header,
header#product_header {
  background-repeat: no-repeat;
  background-position: center center;
  position: relative;
}

header#home_header::after,
header#product_header::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(48, 13, 1, 0.6862745098);
}

header#home_header .header_content,
header#product_header .header_content {
  display: flex;
  flex-flow: column;
  align-content: center;
  align-items: center;
  position: relative;
  color: #fff;
  z-index: 55;
  text-align: center;
  width: 100%;
  padding: 0px 20px;
}

header#home_header .header_content h1,
header#product_header .header_content h1 {
  text-transform: uppercase;
  font-size: 55px;
  letter-spacing: 2px;
  font-weight: 700;
}

header#home_header .header_content p,
header#product_header .header_content p {
  line-height: 30px;
  margin: 0 auto 25px auto;
  max-width: 600px;
}

header#home_header .header_content .header_content__buttons,
header#product_header .header_content .header_content__buttons {
  display: flex;
  justify-content: center;
}

header#home_header .header_content .header_content__buttons a,
header#home_header .header_content .header_content__buttons button,
header#product_header .header_content .header_content__buttons a,
header#product_header .header_content .header_content__buttons button {
  display: block;
  padding: 15px 40px;
  border-radius: 16px;
  border: none;
  overflow: hidden;
  color: #fff;
  font-size: 16px;
  letter-spacing: 1px;
  position: relative;
  z-index: 1;
}

header#home_header .header_content .header_content__buttons a,
header#product_header .header_content .header_content__buttons a {
  background: #f55d22;
  margin: 0px 20px 0px 0px;
}

header#home_header .header_content .header_content__buttons a::before,
header#product_header .header_content .header_content__buttons a::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  height: 100%;
  transition: width 0.2s;
  border-radius: 16px;
  z-index: -1;
  background: rgb(203, 62, 7);
}

header#home_header .header_content .header_content__buttons a:hover::before,
header#product_header .header_content .header_content__buttons a:hover::before {
  width: 100%;
}

header#home_header .header_content .header_content__buttons button,
header#product_header .header_content .header_content__buttons button {
  border: 2px solid #fff;
  background: transparent;
  transition: width 0.2s;
  text-transform: uppercase;
}

header#home_header .header_content .header_content__buttons button::before,
header#product_header .header_content .header_content__buttons button::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 0%;
  height: 100%;
  background: rgba(255, 255, 255, 0.316);
  transition: width 0.2s;
  border-radius: 16px;
  z-index: -1;
}

header#home_header .header_content .header_content__buttons button:hover,
header#product_header .header_content .header_content__buttons button:hover {
  border: 2px solid #fff;
}

header#home_header .header_content .header_content__buttons button:hover::before,
header#product_header .header_content .header_content__buttons button:hover::before {
  width: 100%;
}

