@media screen and (max-width: 767px) {
  nav .container .nav_menu .menuBtn {
    display: flex;
  }

  nav .container .nav_menu ul {
    flex-flow: column;
    justify-content: flex-start;
    position: absolute;
    top: 0px;
    width: 70%;
    left: -100%;
    overflow: hidden;
    transition: left 0.3s linear;
    background: #f6f6f8;
    height: 100vh;
  }

  nav .container .nav_menu ul.active_nav {
    left: 0;
  }

  nav .container .nav_menu ul li a {
    height: 60px;
    padding: 30px 20px;
  }
}

